
import React, { useState, useRef, useEffect } from 'react';
import Calendar from 'react-calendar';

import FullCalendar from '@fullcalendar/react' // must go before plugins
import dayGridPlugin from '@fullcalendar/daygrid' // a plugin!
import interactionPlugin from "@fullcalendar/interaction"
import 'react-calendar/dist/Calendar.css';
import "./cal.css"
export default function Cal({ eve}) {


  





  let imp=JSON.parse(localStorage.getItem("datas"))
  const [task, settask] = useState(imp)
  const [show, setShow] = useState(false);
  const [mydata, setmydata] = useState("")
  const [mydate, setmydate] = useState(null)
  const [holdState,setHold]=useState(false)


  useEffect(() => {


    call()



  }, [])



  function call() {



    settask(imp)
    console.log("call",imp)
    var d = task.map(({ data, date }) => ({ title: data, date }));
    console.log("call",d);
    settask(d)
  }
function calling(){
  let ta=JSON.parse(localStorage.getItem("datas"))
  var d = ta.map(({ data, date }) => ({ title: data, date }));
  console.log("call",d);
  settask(d)
  eve()
}


  function handleShow(e) {









    let date = new Date(new Date().setDate(new Date(e.date).getDate()))
    // console.log(date.getDate()+1)

    let data = e.dayEl.innerText.replace(/[0-9]/g, '');

    console.log(data)



    setmydata(data)
    // console.log(data)
    // console.log(date.toLocaleDateString())
    date = date.toISOString().split("T")[0]
    // console.log(date)
    setmydate(date)

    // var d = task.map(({ data, date }) => ({ title: data, date }));
    // console.log("call",d);
    // settask(d)



    document.querySelector("#modal").style.display = "flex"
    // console.log(e)




    let a=JSON.parse(localStorage.getItem("datas"))
    a.forEach(item=>{
      if(item.date==mydate)
      {
        setHold(true);
        
      }
     



    })
    


    // console.log(holdState)






  }



  return (
    <div className='main' style={{ paddingTop: "5px", position: "relative" }}>
      <FullCalendar
        plugins={[dayGridPlugin, interactionPlugin]}
        initialView="dayGridMonth"
        events={task}
        dateClick={(e) => handleShow(e)}



      />

      <div id="modal" style={{ display: "none", position: "absolute", top: "24%", justifyContent: "center", zIndex: "100", width: "100%" }}>

        <form style={{ padding: "10px", background: "white" }}>
          <div class="mb-3">
            <label for="data" class="form-label">Data</label>
            <input type="text" class="form-control" id="data" value={mydata} onChange={(e) => setmydata(e.target.value)} aria-describedby="emailHelp" />

          </div>




          <div class="d-flex justify-content-around">
            <button type="submit" class="btn btn-primary" onClick={e => {
              e.preventDefault();

              document.querySelector("#modal").style.display = "none";


              ////update data


              if (mydata != "" || mydata != null) {

              if(holdState==true){
                let d = imp.map(item => {
                  if (item.date === mydate) {
                    console.log("match")
                    /* This is the part where I need the logic */
                    item.data = mydata
                    return item
                  } else {
                    return item;
                  }
                })
                console.log(true)
                console.log(d)
                localStorage.setItem("datas", JSON.stringify(d))
                setHold(false)
                
              }
              else{
                let x = Math.ceil(Math.random() * 10000);
                let d = {
                  id: x,
                  data: mydata,
                  important: false,
                  completed: false,
                  date: mydate,
                  reminder: "",
                  repeat: ""
                }
                console.log(false)
                let ta=JSON.parse(localStorage.getItem("datas"))
                ta.push(d)
                console.log(d)
                localStorage.setItem("datas", JSON.stringify(ta))
                setHold(false)
              }
              calling()
             



              }














            }} >Submit</button>
            <button class="btn btn-primary" onClick={(e) => {
              document.querySelector("#modal").style.display = "none";
              e.preventDefault()
            }}>Cancel</button>
          </div>
        </form>
      </div>





    </div>
  );
}

